.uc-toggle {
  margin: 5px;
  
  &-title {
    display: inline-block;
    vertical-align: -webkit-baseline-middle;
    min-width: 90px;
  }

  &-label {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }

  & input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  &-label__slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 34px;

    &:before {
      position: absolute;
      content: "";
      height: 26px;
      width: 26px;
      left: 4px;
      bottom: 4px;
      background-color: white;
      -webkit-transition: .4s;
      transition: .4s;
      border-radius: 50%;
    }
  }

  input:focus + &-label__slider {
    box-shadow: 0 0 1px #2196F3;
  }

  input:checked + &-label__slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
}